import { useState } from "react";
import { Button } from "../../shared/ui/button";
import { Card, CardContent, CardHeader, CardTitle } from "../../shared/ui/card";
 
import { Label } from "../../shared/ui/label";
import { Switch } from "../../shared/ui/switch";
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from "../../shared/ui/select";
import { Tabs, TabsContent, TabsList, TabsTrigger } from "../../shared/ui/tabs";
import { 
  Settings as SettingsIcon, 
  Bell, 
  Moon, 
  Volume2, 
  Shield, 
  Download,
  Trash2,
  Save,
  ArrowLeft
} from "lucide-react";
interface SettingsProps {
  onBackToDashboard?: () => void;
}

const Settings = ({ onBackToDashboard }: SettingsProps) => {
  const [settings, setSettings] = useState({
    notifications: {
      dailyReminder: true,
      lessonComplete: true,
      streakWarning: true,
      achievement: true,
      emailUpdates: false
    },
    appearance: {
      theme: "auto",
      language: "vi",
      fontSize: "medium"
    },
    audio: {
      soundEffects: true,
      voiceGuidance: true,
      volume: 70
    },
    privacy: {
      dataCollection: true,
      analytics: true,
      crashReports: true
    }
  });

  const handleNotificationChange = (key: string, value: boolean) => {
    setSettings(prev => ({
      ...prev,
      notifications: {
        ...prev.notifications,
        [key]: value
      }
    }));
  };

  const handleAppearanceChange = (key: string, value: string) => {
    setSettings(prev => ({
      ...prev,
      appearance: {
        ...prev.appearance,
        [key]: value
      }
    }));
  };

  const handleAudioChange = (key: string, value: boolean | number) => {
    setSettings(prev => ({
      ...prev,
      audio: {
        ...prev.audio,
        [key]: value
      }
    }));
  };

  const handlePrivacyChange = (key: string, value: boolean) => {
    setSettings(prev => ({
      ...prev,
      privacy: {
        ...prev.privacy,
        [key]: value
      }
    }));
  };

  const handleSave = () => {
    // Save settings logic here
    alert("Ci t  c lu!");
  };

  const handleExportData = () => {
    // Export data logic here
    alert("D liu  c xut!");
  };

  const handleDeleteAccount = () => {
    if (confirm("Bn c chc chn mun xa ti khon? Hnh ng ny khng th hon tc.")) {
      alert("Ti khon  c xa!");
    }
  };

  return (
    <div className="min-h-screen bg-background pb-24">
      <main className="container mx-auto px-4 py-8">
        <div className="max-w-4xl mx-auto">
          {/* Header */}
          <div className="flex items-center gap-4 mb-8">
            <Button variant="ghost" size="icon" onClick={onBackToDashboard}>
              <ArrowLeft className="w-5 h-5" />
            </Button>
            <div>
              <h1 className="text-3xl font-bold">Ci t</h1>
              <p className="text-muted-foreground">Qun l ti khon v ty chn ng dng</p>
            </div>
          </div>

          <Tabs defaultValue="notifications" className="w-full">
            <TabsList className="grid w-full grid-cols-5">
              <TabsTrigger value="notifications">Thng bo</TabsTrigger>
              <TabsTrigger value="appearance">Giao din</TabsTrigger>
              <TabsTrigger value="audio">m thanh</TabsTrigger>
              <TabsTrigger value="privacy">Quyn ring t</TabsTrigger>
              <TabsTrigger value="account">Ti khon</TabsTrigger>
            </TabsList>

            <TabsContent value="notifications" className="space-y-6">
              <Card>
                <CardHeader>
                  <CardTitle className="flex items-center gap-2">
                    <Bell className="w-5 h-5 text-blue-600" />
                    Thng bo
                  </CardTitle>
                </CardHeader>
                <CardContent className="space-y-6">
                  <div className="space-y-4">
                    <div className="flex items-center justify-between">
                      <div>
                        <Label className="text-base font-medium">Nhc nh hc tp hng ngy</Label>
                        <p className="text-sm text-muted-foreground">
                          Nhn thng bo nhc nh hc tp mi ngy
                        </p>
                      </div>
                      <Switch
                        checked={settings.notifications.dailyReminder}
                        onCheckedChange={(value) => handleNotificationChange('dailyReminder', value)}
                      />
                    </div>

                    <div className="flex items-center justify-between">
                      <div>
                        <Label className="text-base font-medium">Hon thnh bi hc</Label>
                        <p className="text-sm text-muted-foreground">
                          Thng bo khi hon thnh bi hc
                        </p>
                      </div>
                      <Switch
                        checked={settings.notifications.lessonComplete}
                        onCheckedChange={(value) => handleNotificationChange('lessonComplete', value)}
                      />
                    </div>

                    <div className="flex items-center justify-between">
                      <div>
                        <Label className="text-base font-medium">Cnh bo chui ngy</Label>
                        <p className="text-sm text-muted-foreground">
                          Thng bo khi chui ngy hc c nguy c b mt
                        </p>
                      </div>
                      <Switch
                        checked={settings.notifications.streakWarning}
                        onCheckedChange={(value) => handleNotificationChange('streakWarning', value)}
                      />
                    </div>

                    <div className="flex items-center justify-between">
                      <div>
                        <Label className="text-base font-medium">Thnh tch mi</Label>
                        <p className="text-sm text-muted-foreground">
                          Thng bo khi t c thnh tch mi
                        </p>
                      </div>
                      <Switch
                        checked={settings.notifications.achievement}
                        onCheckedChange={(value) => handleNotificationChange('achievement', value)}
                      />
                    </div>

                    <div className="flex items-center justify-between">
                      <div>
                        <Label className="text-base font-medium">Cp nht qua email</Label>
                        <p className="text-sm text-muted-foreground">
                          Nhn bn tin v cp nht qua email
                        </p>
                      </div>
                      <Switch
                        checked={settings.notifications.emailUpdates}
                        onCheckedChange={(value) => handleNotificationChange('emailUpdates', value)}
                      />
                    </div>
                  </div>
                </CardContent>
              </Card>
            </TabsContent>

            <TabsContent value="appearance" className="space-y-6">
              <Card>
                <CardHeader>
                  <CardTitle className="flex items-center gap-2">
                    <Moon className="w-5 h-5 text-blue-600" />
                    Giao din
                  </CardTitle>
                </CardHeader>
                <CardContent className="space-y-6">
                  <div className="space-y-4">
                    <div className="space-y-2">
                      <Label>Ch </Label>
                      <Select 
                        value={settings.appearance.theme} 
                        onValueChange={(value) => handleAppearanceChange('theme', value)}
                      >
                        <SelectTrigger>
                          <SelectValue />
                        </SelectTrigger>
                        <SelectContent>
                          <SelectItem value="light">Sng</SelectItem>
                          <SelectItem value="dark">Ti</SelectItem>
                          <SelectItem value="auto">T ng</SelectItem>
                        </SelectContent>
                      </Select>
                    </div>

                    <div className="space-y-2">
                      <Label>Ngn ng</Label>
                      <Select 
                        value={settings.appearance.language} 
                        onValueChange={(value) => handleAppearanceChange('language', value)}
                      >
                        <SelectTrigger>
                          <SelectValue />
                        </SelectTrigger>
                        <SelectContent>
                          <SelectItem value="vi">Ting Vit</SelectItem>
                          <SelectItem value="en">English</SelectItem>
                        </SelectContent>
                      </Select>
                    </div>

                    <div className="space-y-2">
                      <Label>C ch</Label>
                      <Select 
                        value={settings.appearance.fontSize} 
                        onValueChange={(value) => handleAppearanceChange('fontSize', value)}
                      >
                        <SelectTrigger>
                          <SelectValue />
                        </SelectTrigger>
                        <SelectContent>
                          <SelectItem value="small">Nh</SelectItem>
                          <SelectItem value="medium">Trung bnh</SelectItem>
                          <SelectItem value="large">Ln</SelectItem>
                        </SelectContent>
                      </Select>
                    </div>
                  </div>
                </CardContent>
              </Card>
            </TabsContent>

            <TabsContent value="audio" className="space-y-6">
              <Card>
                <CardHeader>
                  <CardTitle className="flex items-center gap-2">
                    <Volume2 className="w-5 h-5 text-blue-600" />
                    m thanh
                  </CardTitle>
                </CardHeader>
                <CardContent className="space-y-6">
                  <div className="space-y-4">
                    <div className="flex items-center justify-between">
                      <div>
                        <Label className="text-base font-medium">Hiu ng m thanh</Label>
                        <p className="text-sm text-muted-foreground">
                          Pht m thanh khi tng tc vi ng dng
                        </p>
                      </div>
                      <Switch
                        checked={settings.audio.soundEffects}
                        onCheckedChange={(value) => handleAudioChange('soundEffects', value)}
                      />
                    </div>

                    <div className="flex items-center justify-between">
                      <div>
                        <Label className="text-base font-medium">Hng dn bng ging ni</Label>
                        <p className="text-sm text-muted-foreground">
                          Pht hng dn bng ging ni trong bi hc
                        </p>
                      </div>
                      <Switch
                        checked={settings.audio.voiceGuidance}
                        onCheckedChange={(value) => handleAudioChange('voiceGuidance', value)}
                      />
                    </div>

                    <div className="space-y-2">
                      <Label>m lng ({settings.audio.volume}%)</Label>
                      <input
                        type="range"
                        min="0"
                        max="100"
                        value={settings.audio.volume}
                        onChange={(e) => handleAudioChange('volume', parseInt(e.target.value))}
                        className="w-full h-2 bg-gray-200 rounded-lg appearance-none cursor-pointer"
                      />
                    </div>
                  </div>
                </CardContent>
              </Card>
            </TabsContent>

            <TabsContent value="privacy" className="space-y-6">
              <Card>
                <CardHeader>
                  <CardTitle className="flex items-center gap-2">
                    <Shield className="w-5 h-5 text-blue-600" />
                    Quyn ring t
                  </CardTitle>
                </CardHeader>
                <CardContent className="space-y-6">
                  <div className="space-y-4">
                    <div className="flex items-center justify-between">
                      <div>
                        <Label className="text-base font-medium">Thu thp d liu hc tp</Label>
                        <p className="text-sm text-muted-foreground">
                          Cho php thu thp d liu  ci thin tri nghim hc tp
                        </p>
                      </div>
                      <Switch
                        checked={settings.privacy.dataCollection}
                        onCheckedChange={(value) => handlePrivacyChange('dataCollection', value)}
                      />
                    </div>

                    <div className="flex items-center justify-between">
                      <div>
                        <Label className="text-base font-medium">Phn tch s dng</Label>
                        <p className="text-sm text-muted-foreground">
                          Gi d liu phn tch  ci thin ng dng
                        </p>
                      </div>
                      <Switch
                        checked={settings.privacy.analytics}
                        onCheckedChange={(value) => handlePrivacyChange('analytics', value)}
                      />
                    </div>

                    <div className="flex items-center justify-between">
                      <div>
                        <Label className="text-base font-medium">Bo co li</Label>
                        <p className="text-sm text-muted-foreground">
                          T ng gi bo co li  ci thin ng dng
                        </p>
                      </div>
                      <Switch
                        checked={settings.privacy.crashReports}
                        onCheckedChange={(value) => handlePrivacyChange('crashReports', value)}
                      />
                    </div>
                  </div>
                </CardContent>
              </Card>
            </TabsContent>

            <TabsContent value="account" className="space-y-6">
              <Card>
                <CardHeader>
                  <CardTitle className="flex items-center gap-2">
                    <SettingsIcon className="w-5 h-5 text-blue-600" />
                    Ti khon
                  </CardTitle>
                </CardHeader>
                <CardContent className="space-y-6">
                  <div className="space-y-4">
                    <div className="space-y-2">
                      <Label>Xut d liu</Label>
                      <p className="text-sm text-muted-foreground">
                        Ti xung tt c d liu hc tp ca bn
                      </p>
                      <Button variant="outline" onClick={handleExportData}>
                        <Download className="w-4 h-4 mr-2" />
                        Xut d liu
                      </Button>
                    </div>

                    <div className="space-y-2">
                      <Label>i mt khu</Label>
                      <p className="text-sm text-muted-foreground">
                        Thay i mt khu ti khon
                      </p>
                      <Button variant="outline">
                        i mt khu
                      </Button>
                    </div>

                    <div className="pt-4 border-t">
                      <div className="space-y-2">
                        <Label className="text-destructive">Xa ti khon</Label>
                        <p className="text-sm text-muted-foreground">
                          Xa vnh vin ti khon v tt c d liu
                        </p>
                        <Button variant="destructive" onClick={handleDeleteAccount}>
                          <Trash2 className="w-4 h-4 mr-2" />
                          Xa ti khon
                        </Button>
                      </div>
                    </div>
                  </div>
                </CardContent>
              </Card>
            </TabsContent>
          </Tabs>

          {/* Save Button */}
          <div className="flex justify-end mt-8">
            <Button onClick={handleSave} className="bg-blue-600 hover:bg-blue-700">
              <Save className="w-4 h-4 mr-2" />
              Lu ci t
            </Button>
          </div>
        </div>
      </main>
    </div>
  );
};

export default Settings;
