import React, { useState, useEffect } from "react";
import "./QuizResultScreen.css";

type QuestionResult = {
  id: string;
  question: string;
  userAnswer: string;
  correctAnswer: string;
  isCorrect: boolean;
  explanation?: string;
  videoUrl?: string;
};

type Props = {
  correct: number;
  total: number;
  onRetry?: () => void;
  onContinue?: () => void;
  lessonId?: string;
};

export const QuizResultDetailScreen: React.FC<Props> = ({ 
  correct, 
  total, 
  onRetry, 
  onContinue,
  lessonId = "1"
}) => {
  const percent = total ? Math.round((correct / total) * 100) : 0;
  const [displayScore, setDisplayScore] = useState(0);
  const [isAnimating, setIsAnimating] = useState(true);
  const [showDetails, setShowDetails] = useState(false);

  // Mock question results data
  const questionResults: QuestionResult[] = [
    {
      id: "1",
      question: "K hiu no c ngha l 'Xin cho'?",
      userAnswer: "Vy tay",
      correctAnswer: "Vy tay",
      isCorrect: true,
      explanation: "ng! Vy tay l cch cho hi c bn trong NNKH.",
      videoUrl: "/resources/videos/Cho.mp4"
    },
    {
      id: "2", 
      question: "K hiu 'Cm n' c thc hin nh th no?",
      userAnswer: "Gt u",
      correctAnswer: "a tay ln ngc",
      isCorrect: false,
      explanation: "Sai ri! K hiu 'Cm n' l a tay ln ngc, khng phi gt u.",
      videoUrl: "/resources/videos/xin li.mp4"
    },
    {
      id: "3",
      question: "K hiu 'Gia nh' c th hin bng cch no?",
      userAnswer: "Ch vo ngi khc",
      correctAnswer: "Ch vo ngi khc",
      isCorrect: true,
      explanation: "Chnh xc! Ch vo ngi khc l k hiu cho 'gia nh'.",
      videoUrl: "/resources/videos/b m.mp4"
    },
    {
      id: "4",
      question: "K hiu 'Vui mng' c c im g?",
      userAnswer: "N ci",
      correctAnswer: "N ci v v tay",
      isCorrect: false,
      explanation: "Gn ng! 'Vui mng' bao gm c n ci v v tay.",
      videoUrl: "/resources/videos/vui mng - nam.mp4"
    },
    {
      id: "5",
      question: "K hiu s '1' c th hin bng ngn tay no?",
      userAnswer: "Ngn tr",
      correctAnswer: "Ngn tr",
      isCorrect: true,
      explanation: "ng! S 1 c th hin bng ngn tr.",
      videoUrl: "/resources/videos/1.mp4"
    }
  ];

  // Score animation effect
  useEffect(() => {
    const duration = 2000;
    const steps = 60;
    const increment = percent / steps;
    
    let current = 0;
    const timer = setInterval(() => {
      current += increment;
      if (current >= percent) {
        setDisplayScore(percent);
        setIsAnimating(false);
        clearInterval(timer);
      } else {
        setDisplayScore(Math.floor(current));
      }
    }, duration / steps);
    
    return () => clearInterval(timer);
  }, [percent]);

  const getScoreMessage = () => {
    if (percent >= 90) return "Xut sc! Bn  thnh tho!";
    if (percent >= 70) return "Tt lm! Bn ang tin b!";
    if (percent >= 50) return "Kh tt! Hy tip tc c gng!";
    return "ng b cuc! Luyn tp s lm nn hon ho!";
  };

  const getScoreColor = () => {
    if (percent >= 90) return "text-green-600";
    if (percent >= 70) return "text-blue-600";
    if (percent >= 50) return "text-yellow-600";
    return "text-red-600";
  };

  return (
    <div className="min-h-screen bg-gradient-to-br from-gray-50 to-white" data-testid="quiz-result-detail-screen">
      <div className="w-full max-w-4xl mx-auto p-4">
        {/* Header with Score */}
        <div className="text-center mb-8">
          <div className={`mb-4 ${isAnimating ? 'animate-score-count' : ''}`}>
            <div className={`text-6xl font-bold mb-2 ${getScoreColor()}`}>
              {displayScore}%
            </div>
          </div>
          
          <h1 className="text-3xl font-bold text-gray-800 mb-2">
            {getScoreMessage()}
          </h1>
          
          <p className="text-lg text-gray-600 mb-6">
            Bn  tr li ng {correct}/{total} cu hi
          </p>

          <button
            onClick={() => setShowDetails(!showDetails)}
            className="px-6 py-3 bg-blue-500 hover:bg-blue-600 text-white font-semibold rounded-xl transition-all duration-200 hover:scale-105"
          >
            {showDetails ? "n chi tit" : "Xem chi tit tng cu"}
          </button>
        </div>

        {/* Question Results Detail */}
        {showDetails && (
          <div className="space-y-4 mb-8">
            <h2 className="text-2xl font-bold text-gray-800 text-center mb-6">
              Chi tit tng cu hi
            </h2>
            
            {questionResults.map((result, index) => (
              <div
                key={result.id}
                className={`p-6 rounded-xl border-2 transition-all duration-200 ${
                  result.isCorrect
                    ? "border-green-200 bg-green-50 hover:bg-green-100"
                    : "border-red-200 bg-red-50 hover:bg-red-100"
                }`}
                style={{ animationDelay: `${index * 100}ms` }}
              >
                <div className="flex items-start justify-between mb-4">
                  <div className="flex-1">
                    <h3 className="text-lg font-semibold text-gray-800 mb-2">
                      Cu {result.id}: {result.question}
                    </h3>
                    
                    <div className="space-y-2">
                      <div className="flex items-center space-x-2">
                        <span className="text-sm font-medium text-gray-600">Cu tr li ca bn:</span>
                        <span className={`px-3 py-1 rounded-full text-sm font-medium ${
                          result.isCorrect 
                            ? "bg-green-100 text-green-800" 
                            : "bg-red-100 text-red-800"
                        }`}>
                          {result.userAnswer}
                        </span>
                      </div>
                      
                      <div className="flex items-center space-x-2">
                        <span className="text-sm font-medium text-gray-600">p n ng:</span>
                        <span className="px-3 py-1 rounded-full text-sm font-medium bg-blue-100 text-blue-800">
                          {result.correctAnswer}
                        </span>
                      </div>
                    </div>
                  </div>
                  
                  <div className="flex items-center space-x-2 ml-4">
                    <span className="text-3xl">
                      {result.isCorrect ? "" : ""}
                    </span>
                    <span className={`text-sm font-medium ${
                      result.isCorrect ? "text-green-600" : "text-red-600"
                    }`}>
                      {result.isCorrect ? "ng" : "Sai"}
                    </span>
                  </div>
                </div>

                {result.explanation && (
                  <div className="mt-4 p-4 bg-white rounded-lg border border-gray-200">
                    <h4 className="font-semibold text-gray-700 mb-2">Gii thch:</h4>
                    <p className="text-gray-600">{result.explanation}</p>
                  </div>
                )}

                {result.videoUrl && (
                  <div className="mt-4">
                    <video
                      controls
                      className="w-full h-48 bg-gray-100 rounded-lg"
                      poster="/placeholder-video.jpg"
                    >
                      <source src={result.videoUrl} type="video/mp4" />
                      Trnh duyt ca bn khng h tr video.
                    </video>
                  </div>
                )}
              </div>
            ))}
          </div>
        )}

        {/* Action Buttons */}
        <div className="flex flex-col sm:flex-row gap-4 justify-center">
          <button
            onClick={onRetry}
            className="px-8 py-4 bg-orange-500 hover:bg-orange-600 text-white font-semibold text-lg rounded-xl transition-all duration-200 hover:scale-105 active:scale-95 shadow-lg"
          >
            Lm li bi
          </button>
          
          <button
            onClick={onContinue}
            className="px-8 py-4 bg-blue-500 hover:bg-blue-600 text-white font-semibold text-lg rounded-xl transition-all duration-200 hover:scale-105 active:scale-95 shadow-lg"
          >
            Tip tc
          </button>
        </div>
      </div>
    </div>
  );
};

export default QuizResultDetailScreen;
