import React, { useState, useEffect } from 'react';
import { Button } from '../../../shared/ui/button';
import { Card, CardContent, CardHeader, CardTitle } from '../../../shared/ui/card';
import { Badge } from '../../../shared/ui/badge';
import { Play, RotateCcw, CheckCircle, X, Clock, MessageCircle, Mic, Star } from 'lucide-react';

interface ConversationPracticeProps {
  onComplete: (score: number, timeSpent: number) => void;
  onClose: () => void;
}

const ConversationPractice: React.FC<ConversationPracticeProps> = ({ onComplete, onClose }) => {
  const [currentIndex, setCurrentIndex] = useState(0);
  const [isPlaying, setIsPlaying] = useState(false);
  const [score, setScore] = useState(0);
  const [timeSpent, setTimeSpent] = useState(0);
  const [showResult, setShowResult] = useState(false);
  const [isRecording, setIsRecording] = useState(false);
  const [showInstructions, setShowInstructions] = useState(true);

  // Conversation scenarios
  const scenarios = [
    {
      id: 1,
      title: "Ln u gp g",
      context: "Bn gp mt ngi bn mi ti mt s kin",
      conversation: [
        { speaker: "A", text: "Xin cho! Mnh l Minh.", video: "/resources/videos/Cho.mp4" },
        { speaker: "B", text: "Cho bn! Mnh l Lan. Rt vui c gp bn.", video: "/resources/videos/vui mng - nam.mp4" },
        { speaker: "A", text: "Bn c hc ngn ng k hiu khng?", video: "/resources/videos/Cho.mp4" },
        { speaker: "B", text: "C, mnh mi bt u hc. Cn bn th sao?", video: "/resources/videos/Cho.mp4" }
      ],
      userRole: "B"
    },
    {
      id: 2,
      title: "Thm hi gia nh",
      context: "Bn ang hi thm v gia nh ca mt ngi bn",
      conversation: [
        { speaker: "A", text: "Gia nh bn c my ngi?", video: "/resources/videos/b m.mp4" },
        { speaker: "B", text: "Nh mnh c bn ngi: b, m, anh trai v mnh.", video: "/resources/videos/b m.mp4" },
        { speaker: "A", text: "Anh trai bn bao nhiu tui?", video: "/resources/videos/1.mp4" },
        { speaker: "B", text: "Anh y hai mi lm tui.", video: "/resources/videos/1.mp4" }
      ],
      userRole: "B"
    },
    {
      id: 3,
      title: "Chia s cm xc",
      context: "Bn ang chia s cm xc vi mt ngi bn thn",
      conversation: [
        { speaker: "A", text: "Hm nay bn cm thy th no?", video: "/resources/videos/vui mng - nam.mp4" },
        { speaker: "B", text: "Mnh rt vui mng v c bi kim tra ngy mai.", video: "/resources/videos/vui mng - nam.mp4" },
        { speaker: "A", text: "Ti sao bn li vui mng?", video: "/resources/videos/Cho.mp4" },
        { speaker: "B", text: "V mnh  chun b rt k v t tin s lm tt.", video: "/resources/videos/t_tin.mp4" }
      ],
      userRole: "B"
    }
  ];

  const currentScenario = scenarios[currentIndex];
  const currentConversation = currentScenario.conversation;

  useEffect(() => {
    let interval: NodeJS.Timeout;
    if (isPlaying) {
      interval = setInterval(() => {
        setTimeSpent(prev => prev + 1);
      }, 1000);
    }
    return () => clearInterval(interval);
  }, [isPlaying]);

  const handleNext = () => {
    if (currentIndex < scenarios.length - 1) {
      setCurrentIndex(prev => prev + 1);
      setScore(prev => prev + 1); // Assume user practiced correctly
    } else {
      setIsPlaying(false);
      setShowResult(true);
    }
  };

  const handleRestart = () => {
    setCurrentIndex(0);
    setScore(0);
    setTimeSpent(0);
    setShowResult(false);
    setIsPlaying(false);
    setIsRecording(false);
    setShowInstructions(true);
  };

  const formatTime = (seconds: number) => {
    const mins = Math.floor(seconds / 60);
    const secs = seconds % 60;
    return `${mins}:${secs.toString().padStart(2, '0')}`;
  };

  if (showResult) {
    const accuracy = Math.round((score / scenarios.length) * 100);
    const xpEarned = Math.round(score * 12 + (accuracy > 80 ? 25 : 0));

    return (
      <div className="fixed inset-0 bg-black/50 flex items-center justify-center p-4 z-50">
        <Card className="w-full max-w-md bg-gradient-to-br from-orange-50 to-red-50 border-orange-200">
          <CardHeader className="text-center">
            <div className="mx-auto w-16 h-16 bg-orange-100 rounded-full flex items-center justify-center mb-4">
              <MessageCircle className="w-8 h-8 text-orange-600" />
            </div>
            <CardTitle className="text-2xl text-orange-800">Hi thoi hon thnh!</CardTitle>
            <p className="text-muted-foreground">Bn  thc hnh giao tip rt tt</p>
          </CardHeader>
          <CardContent className="space-y-6">
            <div className="grid grid-cols-2 gap-4">
              <div className="text-center p-4 bg-white rounded-lg">
                <div className="text-2xl font-bold text-blue-600">{score}/{scenarios.length}</div>
                <div className="text-sm text-muted-foreground">Tnh hung  luyn</div>
              </div>
              <div className="text-center p-4 bg-white rounded-lg">
                <div className="text-2xl font-bold text-orange-600">{formatTime(timeSpent)}</div>
                <div className="text-sm text-muted-foreground">Thi gian luyn</div>
              </div>
            </div>

            <div className="text-center p-4 bg-gradient-to-r from-yellow-100 to-orange-100 rounded-lg">
              <div className="flex items-center justify-center gap-2 mb-2">
                <Star className="w-5 h-5 text-yellow-500" />
                <span className="text-lg font-semibold text-orange-700">+{xpEarned} XP</span>
              </div>
              <div className="text-sm text-orange-600">im kinh nghim kim c</div>
            </div>

            <div className="p-4 bg-orange-50 rounded-lg">
              <h4 className="font-semibold text-orange-800 mb-2">Li khuyn:</h4>
              <p className="text-sm text-orange-700">
                Tip tc luyn tp hi thoi  ci thin k nng giao tip thc t!
              </p>
            </div>

            <div className="flex gap-3">
              <Button onClick={handleRestart} variant="outline" className="flex-1">
                <RotateCcw className="w-4 h-4 mr-2" />
                Luyn li
              </Button>
              <Button onClick={() => onComplete(score, timeSpent)} className="flex-1 bg-orange-600 hover:bg-orange-700">
                <CheckCircle className="w-4 h-4 mr-2" />
                Hon thnh
              </Button>
            </div>
          </CardContent>
        </Card>
      </div>
    );
  }

  if (showInstructions) {
    return (
      <div className="fixed inset-0 bg-black/50 flex items-center justify-center p-4 z-50">
        <Card className="w-full max-w-md bg-gradient-to-br from-orange-50 to-red-50 border-orange-200">
          <CardHeader className="text-center">
            <div className="mx-auto w-16 h-16 bg-orange-100 rounded-full flex items-center justify-center mb-4">
              <MessageCircle className="w-8 h-8 text-orange-600" />
            </div>
            <CardTitle className="text-2xl text-orange-800">Luyn hi thoi</CardTitle>
            <p className="text-muted-foreground">Hng dn thc hnh giao tip</p>
          </CardHeader>
          <CardContent className="space-y-4">
            <div className="space-y-3 text-sm">
              <div className="flex items-start gap-3">
                <div className="w-6 h-6 bg-orange-100 rounded-full flex items-center justify-center flex-shrink-0 mt-0.5">
                  <span className="text-xs font-bold text-orange-600">1</span>
                </div>
                <p>c tnh hung v vai tr ca bn</p>
              </div>
              <div className="flex items-start gap-3">
                <div className="w-6 h-6 bg-orange-100 rounded-full flex items-center justify-center flex-shrink-0 mt-0.5">
                  <span className="text-xs font-bold text-orange-600">2</span>
                </div>
                <p>Xem video mu ca i tc</p>
              </div>
              <div className="flex items-start gap-3">
                <div className="w-6 h-6 bg-orange-100 rounded-full flex items-center justify-center flex-shrink-0 mt-0.5">
                  <span className="text-xs font-bold text-orange-600">3</span>
                </div>
                <p>Thc hin k hiu phn hi ca bn</p>
              </div>
              <div className="flex items-start gap-3">
                <div className="w-6 h-6 bg-orange-100 rounded-full flex items-center justify-center flex-shrink-0 mt-0.5">
                  <span className="text-xs font-bold text-orange-600">4</span>
                </div>
                <p>Ghi m v so snh vi video mu</p>
              </div>
            </div>

            <div className="flex gap-3">
              <Button onClick={onClose} variant="outline" className="flex-1">
                <X className="w-4 h-4 mr-2" />
                Thot
              </Button>
              <Button onClick={() => setShowInstructions(false)} className="flex-1 bg-orange-600 hover:bg-orange-700">
                <Play className="w-4 h-4 mr-2" />
                Bt u
              </Button>
            </div>
          </CardContent>
        </Card>
      </div>
    );
  }

  return (
    <div className="fixed inset-0 bg-black/50 flex items-center justify-center p-4 z-50">
      <Card className="w-full max-w-4xl bg-white max-h-[90vh] flex flex-col">
        <CardHeader className="bg-gradient-to-r from-orange-50 to-red-50 flex-shrink-0">
          <div className="flex items-center justify-between">
            <div>
              <CardTitle className="text-xl flex items-center gap-2">
                <MessageCircle className="w-5 h-5 text-orange-600" />
                Luyn hi thoi
              </CardTitle>
              <p className="text-sm text-muted-foreground mt-1">
                Tnh hung {currentIndex + 1} / {scenarios.length}: {currentScenario.title}
              </p>
            </div>
            <div className="flex items-center gap-4">
              <div className="flex items-center gap-2 text-sm text-muted-foreground">
                <Clock className="w-4 h-4" />
                <span>{formatTime(timeSpent)}</span>
              </div>
              <Button variant="ghost" size="sm" onClick={onClose}>
                <X className="w-4 h-4" />
              </Button>
            </div>
          </div>
        </CardHeader>
        
        <CardContent className="p-6 flex-1 overflow-y-auto">
          <div className="space-y-6">
            {/* Scenario context */}
            <div className="p-4 bg-blue-50 rounded-lg">
              <h3 className="font-semibold text-blue-800 mb-2">Tnh hung:</h3>
              <p className="text-blue-700">{currentScenario.context}</p>
              <div className="mt-2">
                <Badge className="bg-blue-100 text-blue-700">
                  Vai tr ca bn: {currentScenario.userRole === 'A' ? 'Ngi A' : 'Ngi B'}
                </Badge>
              </div>
            </div>

            {/* Conversation flow */}
            <div className="space-y-4">
              <h3 className="text-lg font-semibold text-gray-800">Cuc hi thoi:</h3>
              <div className="space-y-3">
                {currentConversation.map((message, index) => (
                  <div key={index} className={`flex ${message.speaker === currentScenario.userRole ? 'justify-end' : 'justify-start'}`}>
                    <div className={`max-w-xs lg:max-w-md p-3 rounded-lg ${
                      message.speaker === currentScenario.userRole 
                        ? 'bg-orange-100 text-orange-800' 
                        : 'bg-gray-100 text-gray-800'
                    }`}>
                      <div className="flex items-center gap-2 mb-2">
                        <span className="text-xs font-medium">
                          {message.speaker === 'A' ? 'Ngi A' : 'Ngi B'}
                        </span>
                        {message.speaker === currentScenario.userRole && (
                          <Badge className="bg-orange-200 text-orange-700 text-xs">Bn</Badge>
                        )}
                      </div>
                      <p className="text-sm mb-2">{message.text}</p>
                      <video 
                        className="w-full rounded"
                        src={message.video}
                        controls
                        playsInline
                        muted
                      />
                    </div>
                  </div>
                ))}
              </div>
            </div>

            {/* Practice area */}
            <div className="p-4 bg-gray-50 rounded-lg">
              <h4 className="font-semibold text-gray-800 mb-3">Khu vc thc hnh ca bn:</h4>
              
              {/* Recording area */}
              <div className="bg-gray-200 rounded-lg h-32 flex items-center justify-center border-2 border-dashed border-gray-300 mb-4">
                <div className="text-center">
                  <Mic className="w-8 h-8 text-gray-400 mx-auto mb-2" />
                  <p className="text-gray-500 text-sm">Ghi m phn hi ca bn</p>
                  <p className="text-gray-400 text-xs mt-1">S dng k hiu ph hp vi vai tr</p>
                </div>
              </div>

              {/* Recording controls */}
              <div className="flex gap-3">
                <Button 
                  onClick={() => setIsRecording(!isRecording)}
                  className={`flex-1 ${isRecording ? 'bg-red-600 hover:bg-red-700' : 'bg-green-600 hover:bg-green-700'}`}
                >
                  <Mic className="w-4 h-4 mr-2" />
                  {isRecording ? 'Dng ghi m' : 'Bt u ghi m'}
                </Button>
                <Button 
                  onClick={handleNext}
                  variant="outline"
                  className="flex-1"
                >
                  <CheckCircle className="w-4 h-4 mr-2" />
                  Hon thnh
                </Button>
              </div>
            </div>

            {/* Tips */}
            <div className="p-3 bg-yellow-50 rounded-lg">
              <h4 className="font-semibold text-yellow-800 mb-2">Mo giao tip:</h4>
              <ul className="text-sm text-yellow-700 space-y-1">
                <li> Ch  n ng cnh v tnh hung</li>
                <li> S dng k hiu ph hp vi vai tr</li>
                <li> Th hin cm xc qua nt mt</li>
                <li> Thc hin k hiu r rng v chnh xc</li>
              </ul>
            </div>
          </div>
        </CardContent>
      </Card>
    </div>
  );
};

export default ConversationPractice;
